@import url('https://fonts.googleapis.com/css2?family=Inconsolata&display=swap');
body {
  background: #0c0c0c;
}

#about {
  margin: 2rem auto;
  font-family: 'Inconsolata', monospace;
  background: #0c0c0c;
  color: #13a10e;
}

.section {
  min-height: 50vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem;
}

.services {
  font-size: 1.5rem;
}
.services ul li {
  padding: 0.5rem;
  text-transform: capitalize;
}
.contacts {
  font-size: 1.5rem;
}

.item {
  display: flex;
  justify-content: flex-start;
  padding: 10px 0;
}
.icon {
  padding: 0 1rem 0 0;
}
.item a {
  color: #13a10e;
}
.phone {
  padding: 0 0 2rem 0;
}
@media screen and (max-width: 720px) {
  .section {
    flex-direction: column-reverse;
  }
  .contacts {
    font-size: 1.2rem;
  }
  .services ul li {
    padding: 0.2rem;
  }
}
